.AboutsUs{
    width: 100%;
    height: 100%;
    padding-left: 5%;
    padding-right: 5%;
    //background: -webkit-linear-gradient(silver, antiquewhite);
    background-color: var(--pagesBackgroundColor);
}

.aboutUsLeftPane{
    float: left;
    margin-right: 10%;
    width: 45%;
    height: 73%;
}

.aboutUsRightPane{
    float: right;
    margin-left: 0%;
    margin-right: 5%;
    width: 40%;
    height: 73%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

    .aboutUsTitle{
        align-self: flex-start;
        font-family: 'Merienda', cursive;
        height: 10%;
        background: -webkit-linear-gradient(var(--primary), blanchedalmond);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 2vw;
    }

    .aboutUsText{
        height: 70%;
        width: 100%;
        font-size: 3vh;
        //font-family: 'Arvo', serif;
        font-family: 'Zilla Slab', serif;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        align-content: flex-start;
        text-align: left;
        justify-content: flex-start;
    }

    .aboutUsText p{
        text-indent: 1.5vh;
    }

    #aboutUsTag{
        align-self: center;
    }
    .aboutUsStatsHolder{
        width: 100%;
        height: 20%;
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        justify-content: space-around;
    }

    .statNumber{
        background: -webkit-linear-gradient( var(--primary), black);
        -webkit-background-clip: text;
        font-size: 4vh;
        -webkit-text-fill-color: transparent;
    }


@media (pointer:none), (pointer:coarse) {
    .AboutsUs{
    }
    .aboutUsLeftPane{
        display: none;
    }

    .aboutUsRightPane{
        width: 95%;
        height: 90%;
    }
        .aboutUsTitle{
            font-size: 3vh;
        }
        .aboutUsText{
            height: 70%;
            width: 100%;
            font-size: 2.5vh;
            font-family: 'Zilla Slab', serif;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            align-content: flex-start;
            text-align: left;
            justify-content: flex-start;
            overflow: hidden;
        }
        .aboutUsStatsHolder{
            width: 100%;
            height: 30%;
            display: flex;
            flex-direction: row;
            align-items: center;
            align-content: center;
            justify-content: space-around;
        }
}