.BurgerMenu{
    display: none;
    width: 15%;
    height: 90%;
    float: right;
    margin-right: 5%;
    padding: 2%;
    z-index: 10;
}

.NavBar{
    float: right;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    margin-right: 2%;
}

.navLi{
    color: white;
    text-align: center;
    list-style: none;
    width: 25%;
    
}

    .navA{
        color: white;
        text-decoration: none;
        font-size: 2.5vh;
        font-family: 'Acme', sans-serif;
    }
    .navA:hover{
        color: grey;
        border-bottom: 2px solid white;
    }


.navLangLi{
    font-weight: bold;
    text-decoration: underline;
    font-size: 2.5vh;
    color: white;
    text-align: center;
    list-style: none;
    width: 10%;
}

.langImage{
    width: 50%;
    height: 50%;
}


@media (pointer:none), (pointer:coarse) {
    .BurgerMenu{
        display: block;
    }
    .NavBar{
        position: fixed;
        top: 0%;
        right: 0;
        width: 100%;
        height: 60%;
        background-color: white;
        display: none;
        flex-direction: row;
        align-items: center;
        align-content: center;
        margin-right: 0%;
        list-style-type: none;
    }
    .navLi{
        display: block;
        margin-bottom: 10%;
        color: white;
        text-align: center;
        list-style: none;
        width: 100%;
    }

    .navA{
        font-size: 3.5vh;
    }

    Link{
        display: block;
        color: white;
        text-decoration: none;
        font-size: 4vh;
        font-family: 'Acme', sans-serif;
    }
    a:hover{
        color: grey;
    }
}