.Home{
    width: 100%;
    height: 100%;
    padding-top: 5%;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: space-around;
    justify-items: center;
    justify-content: space-around;
}
    .homeLogoContainer{
        display: block;
        width: 40%;
        height: 25%;
    }
    .homeText{
        border-left: 5px solid white;
        padding-left: 3%;
        text-align: left;
        width: 40%;
        height: 25%;
        color: white;
        font-size: 5vh;
    }

        .toAnimate{
            width: 100%;
            height: 50%;
        }

    .scrollDown{
        top: 90%;
        position: absolute;
        height: 10%;
        width: 5%;

    }


@media (pointer:none), (pointer:coarse){
    .Home{
        flex-direction: column;
        justify-content: space-evenly;

    }
    .homeLogoContainer{
        width: 80%;
        height: 30%;
    }

    .homeText{
        display: none;
        align-self: center;
        text-align: center;
        height: auto;
        width: 100%;
        color: white;
        font-size: 4vw;
        border-left: 0px solid white;
    }

    .scrollDown{
        top: 80%;
        left: 35%;
        position: absolute;
        height: 20%;
        width: 30%;
    }

}


